#login {
  width: 100%;
  height: 100%;
  background: url('../images/bg-login.jpg') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  text-align: center;

  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: {
      color: rgba(42, 50, 61, .6); } }

  p {
    margin: {
      bottom: 50px; }
    color: $color-soft; }

  h1 {
    font: {
      size: 2em;
      weight: 400; }
    color: $color-soft;
    letter-spacing: -1px;
    margin: {
      top: 20px; } }

  h2 {
    font: {
      size: 1.4em;
      weight: 400; }
    color: $color-soft;
    margin: {
      top: 40px;
      bottom: 10px; } }

  .form {
    input {
      padding: 10px 20px; }

    .btn {
      margin: 0 auto; } } }
